/*销售排行榜*/
<template>
  <div class="salesListCon">
    <div class="pageCon">
      <el-pagination
          @size-change="handleSizeChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="50"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total"
        ></el-pagination>
      <!-- <el-pagination
         @size-change="handleSizeChange"
         background
         layout="prev, pager, next"
        :total="total">
      </el-pagination> -->
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      border
      stripe
      v-loading="loading"
      height="83vh"
      >
      <el-table-column
        type="index"
        header-align="center"
        width="50">
      </el-table-column>
      <el-table-column
        header-align="center"
        width="100"
        prop="num"
        label="销售数量">
      </el-table-column>
      <el-table-column
        header-align="center"
        label="图片"
        sortable
        width="80">
        <template slot-scope="scope">
          <span v-show="scope.row.imgs.length>0">
            <el-tooltip placement="right-start">
              <div slot="content">
                <img v-lazy="bigImgSrc" alt srcset class="bigImgCon">
              </div>
              <img v-lazy="scope.row.imgs[0].path" class="ImgCon" alt srcset ref="user_image" @mouseover="mouseover(scope.row.imgs)" @mousewheel="wheel(scope.row.imgs)">
            </el-tooltip>
          </span>
        </template>
      </el-table-column>
      <el-table-column
        header-align="center"
        label="商品标题"
        show-overflow-tooltip
        sortable>
        <template slot-scope="scope">
          <div>
            <el-input type="textarea" v-model="scope.row.goods_name" resize="none" class="width" :rows="2" placeholder="商品名称" @change="handleChangeData('name',item.name,item.id)" disabled></el-input>
          </div>
          <div>
            <el-input v-model="scope.row.subjects.subjects_one" class="width19" placeholder="主词1" disabled></el-input>
            <el-input v-model="scope.row.subjects.subjects_two" class="width19" placeholder="主词2" disabled></el-input>
            <el-input v-model="scope.row.subjects.subjects_three" class="width19" placeholder="主词3" disabled></el-input>
            <el-input v-model="scope.row.subjects.subjects_four" class="width19" placeholder="主词4" disabled></el-input>
            <el-input v-model="scope.row.subjects.subjects_five" class="width19" placeholder="主词5" disabled></el-input>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        header-align="center"
        label="SKU/颜色/规格"
        show-overflow-tooltip
        width="210">
        <template slot-scope="scope">
          <p>SKU：{{ scope.row.size.sku }}</p>
          <p>颜色：{{ scope.row.size.main_color }}</p>
          <p>规格：{{ scope.row.size.size }}</p>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  let imgIndex = 0;//bigImg索引
export default {
  data(){
    return {
      tableData: [],
      loading: false,
      total: 0,
      pagerCount: 5,//页面按钮数量>=5
      currentPage: 1,//第几页
      bigImgSrc: '',//大图地址
    }
  },
  methods: {
    //改变页码
    handleSizeChange(val) {
      this.initData(val);
    },
    handleCurrentChange(val) {
      this.initData(val);
    },
    //滚动查看图片
    wheel(imgValue,e=event) {
      this.bodyMouseWheel(e);
      //判断浏览器，谷歌|| firefox滑轮事件
      if (e.wheelDelta > 0 || e.detail > 0) {
        //当滑轮向上滚动时
        if(imgValue.length>=imgIndex&&imgIndex>=0){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex--;
        }
        if(imgIndex==-1){
          imgIndex = imgValue.length-1;
          // imgIndex = 3;
        }
      }
      if (e.wheelDelta < 0 || e.detail < 0) {
        //当滑轮向下滚动时
        if(imgValue.length>imgIndex){
          this.bigImgSrc = imgValue[imgIndex].path;
          imgIndex++;
        }
        if(imgValue.length==imgIndex){
          imgIndex = 0;
        }
      }
    },
    mouseover(val){
      this.bigImgSrc = val[0].path;
    },
    bodyMouseWheel(e){
      e.preventDefault();
    },
    initData(val){
      this.loading = true;
      this.postRequest(`/v1/merchantsAnalysis?page=${val}`)
      .then(res => {
        if(res.data.code=='200'){
          this.tableData = res.data.data.data;
          this.loading = false;
          this.total = res.data.data.total;
        }
      })
      .catch(error => {
      })
    }
  },
  created(){
    this.initData(1);
  }
}
</script>

<style lang="scss" scoped>
.pageCon {
  margin-right: 10px;
  text-align: right;
  height: 3vh;
  line-height: 3vh;
  padding: 1vh 0;
  background-color: #fff;
}
.bigImgCon {
  width: 300px;
  cursor: pointer;
}
.ImgCon {
  width: 50px;
  cursor: pointer;
}
</style>
